import {
  Component,
  inject,
  computed,
  ViewChild,
  TemplateRef,
  AfterViewInit,
  ChangeDetectorRef,
} from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { AuthDataService } from '../../../../core/services/auth-data.service';
import { DynamicTabPanelComponent } from '../../../../shared/components/tabs/dynamic-tab-panel/dynamic-tab-panel.component';
import { TabConfig } from '../../../../shared/models/tabs.model';

@Component({
  selector: 'app-store-shell',
  imports: [CommonModule, RouterModule, DynamicTabPanelComponent],
  templateUrl: './app-store-shell.component.html',
  styleUrls: ['./app-store-shell.component.scss'],
})
export class AppStoreShellComponent implements AfterViewInit {
  @ViewChild('contentTemplate') contentTemplate!: TemplateRef<unknown>;

  private readonly authDataService = inject(AuthDataService);
  private readonly cdr = inject(ChangeDetectorRef);

  readonly isAdmin = computed(() => this.authDataService.isSuperAdmin());

  readonly tabs = computed<TabConfig[]>(() => {
    const baseTabs: TabConfig[] = [
      {
        id: 'explore',
        label: 'Explore',
        icon: 'pi pi-compass',
        route: '/app-store',
        level: 0,
      },
      {
        id: 'my-apps',
        label: 'Installed',
        icon: 'pi pi-box',
        route: '/app-store/my-apps',
        level: 0,
      },
    ];

    if (this.isAdmin()) {
      baseTabs.push(
        {
          id: 'my-created-apps',
          label: 'Developer',
          icon: 'pi pi-code',
          route: '/app-store/my-created-apps',
          level: 0,
        },
        {
          id: 'admin',
          label: 'Admin Review',
          icon: 'pi pi-shield',
          route: '/app-store/admin',
          level: 0,
        },
      );
    }

    return baseTabs;
  });

  ngAfterViewInit(): void {
    this.applyTemplateToTabs(this.tabs(), this.contentTemplate);
    this.cdr.detectChanges();
  }

  private applyTemplateToTabs(
    tabs: TabConfig[],
    template: TemplateRef<unknown>,
  ): void {
    for (const tab of tabs) {
      tab.template = template;
      if (tab.children) {
        this.applyTemplateToTabs(tab.children, template);
      }
    }
  }
}
